.App {
  text-align: center;
}

.header {
  background: #002b57;
  color: #fff;
  text-align: center;
  padding: 2rem;
  animation: fadeIn 2s;
}

nav {
  padding: 16px 32px;
}

nav a {
  margin-right: 16px;
}

nav li {
  display: inline;
  margin: 0 20px;
}

nav a {
  text-decoration: none;
  color: #fff;
}

.mainlogo {
  float: left;
  margin-top: -15px;
  margin-left: -19px;
}

.cartoon {
  position: relative;
  width: 47px;
  left: 19%;
  margin-top: -66px;
  animation: fly 5s infinite;
}

@keyframes fly {
  0% {
    left: -10%;
    top: 5px;
  }

  15% {
    left: 105px;
  }

  40% {
    left: 30px;
  }

  65% {
    left: 25px;
  }

  200% {
    left: -200px;
    top: 105px;
  }
}
/* About css*/ 
.details{
  color:#000000;
  font-family: 'Courier New', Courier, monospace;
  font-weight:1000;
  width: 77%;
  padding: 0px 193px;
}

/* choose css */
.Registerbt {
  padding: 10px 130px;
  font-size: 17px;
  border: 4px solid #002b57;
  border-radius: 21px;
  color: #002b57;
  transition: color 0.3s, background-color 0.3s;
  font-family: Georgia, "Times New Roman", Times, serif;
}

.Registerbt:hover {
  color: #fff;
  background-color: #014589;
}

.content {
  display: inline-block;
  width: 310px;
  height: 255px;
  background-color: #002b57;
  padding: 45px 92px;
  border-radius: 44px;
  box-shadow: 54px 42px 62px;
}


/* login css */
.button {
  padding: 10px 70px;
  font-size: 17px;
  border: 4px solid #ffffff;
  border-radius: 21px;
  color: #000000;
  transition: color, background-color 0.3s;
  font-family: Georgia, "Times New Roman", Times, serif;
}

.button:hover {
  color: #ffffff;
  background-color: #002b57;
}

.logbox{
  display: inline-block;
  width: 327px;
  height: 193px;
  background-color: #002b57;
  padding: 29px 53px;
  border-radius: 44px;
  box-shadow: 54px 42px 62px;
}

.data {
  height: 22px;
  width: 255px;
}

.find label{
  position: static;
  color: #ffffff;
}

