.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup-content {
  background: #cdcdcd;
  padding: 76px;
  border-radius: 74px;
  box-shadow: 25px 62px 207px #002b57;

}


.popup-content {
  text-align: center;
}

.popup label {
  display: block;
  margin-bottom: 10px;
}

